import { overflowHidden, overflowVisible } from "../utils/overflow";
import { isScrollable } from "../utils/scrollbar";

const Modal = () => {
    /* Opening modal window function */
    function openModal() {
        /* Get trigger element */
        var modalTrigger = document.querySelectorAll('[data-js-modal-trigger]');
        
  
        /* Set onclick event handler for all trigger elements */
        for(var i = 0; i < modalTrigger.length; i++) {
            modalTrigger[i].onclick = function() {
              var target = this.getAttribute('href').substr(1);
              var modalWindow = document.getElementById(target);

              modalWindow && ( setTimeout(() => {modalWindow.classList.add('open')}, 100), modalWindow.style.display = 'block' );
              
              overflowHidden();
            }
        }
    }
  
    function closeModal(){
      /* Get close button */
      var closeButton = document.querySelectorAll('[data-js-modal-close]');
      var closeOverlay = document.querySelectorAll('[data-js-modal-overlay]');
  
      /* Set onclick event handler for close buttons */
        for(var i = 0; i < closeButton.length; i++) {
          closeButton[i].onclick = function() {
            var modalWindow = this.closest('[data-js-modal]');

            modalWindow && ( setTimeout(() => {modalWindow.style.display = 'none'}, 600), modalWindow.classList.remove('open') );
        
            overflowVisible();
          }
        }   
  
      /* Set onclick event handler for modal overlay */
        for(var i = 0; i < closeOverlay.length; i++) {
          closeOverlay[i].onclick = function() {
            var modalWindow = this.closest('[data-js-modal]');

            modalWindow && ( setTimeout(() => {modalWindow.style.display = 'none'}, 600), modalWindow.classList.remove('open') );
        
            overflowVisible();
          }
        }
  
    }
  
    /* Handling domready event IE9+ */
    function ready(fn) {
      if (document.readyState != 'loading'){
        fn();
      } else {
        document.addEventListener('DOMContentLoaded', fn);
      }
    }
  
    /* Triggering modal window function after dom ready */
    ready(openModal);
    ready(closeModal);
}

export default Modal;